import React, {useState} from 'react';
import AppCard from '../../../@crema/core/AppCard';
import {Box} from '@mui/material';
import {Fonts} from '../../../shared/constants/AppEnums';
import {useIntl} from 'react-intl';
import AppAnimate from '../../../@crema/core/AppAnimate';
import PropTypes from 'prop-types';

import TextField from '@mui/material/TextField';

const OrderAddress = ({data}) => {
  const {messages} = useIntl();
  const [adress, setAdress] = useState(
    JSON.parse(sessionStorage.getItem('OrderAddress')),
  );

  const handleChange = (e) => {
    e.preventDefault();
    setAdress({
      ...adress,

      [e.target.name]: e.target.value.trim(),
    });
  };
  window.sessionStorage.setItem('OrderAddress', JSON.stringify(adress));

  // setAdress("");
  return (
    <AppAnimate animation='transition.slideUpIn' delay={200}>
      <AppCard
        title={
          <Box fontSize={16} fontWeight={Fonts.BOLD}>
            {messages['sidebar.order.orderDetials']}
          </Box>
        }
      >
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mt: 2,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['Region']}: </Box>
          <Box>{data.Region}</Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['City']}: </Box>
          <Box>
            <TextField
              defaultValue={data.city}
              onChange={(e) => handleChange(e)}
              name='city'
              id='formatted-numberformat-input'
              variant='standard'
            />
          </Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['common.address']}: </Box>
          <Box>
            <TextField
              defaultValue={data.house}
              onChange={(e) => handleChange(e)}
              name='house'
              id='formatted-numberformat-input'
              variant='standard'
            />
          </Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['HouseType']}: </Box>
          <Box>
            <TextField
              defaultValue={data.houseType}
              onChange={(e) => handleChange(e)}
              name='houseType'
              id='formatted-numberformat-input'
              variant='standard'
            />
          </Box>
        </Box>
      </AppCard>
    </AppAnimate>
  );
};

export default OrderAddress;

OrderAddress.propTypes = {
  cartItems: PropTypes.array,
  data: PropTypes.object.isRequired,
};
